.carousel .slide {
  position: relative;  
  width: 100%;
}

.carousel .show {
  /* position: absolute; */
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  animation: fade 3s ease-in-out;
}

.carousel .show > div {
  width: 100%;
  height: 100%;
}

.carousel img {
  width: 100%;
  height: 100%;
}

/* @-webkit-keyframes trans {
  from {
    left: 100%;
  }
  to {
    left: 0%;
  }
}

@keyframes trans {
  from {
    left: 100%;
  }
  to {
    left: 0%;
  }
} */

 @keyframes fade {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}

.carousel .not-show {
  display: none;
}

.carousel .prev {
  position: absolute;
  top: 50%;
  left: 1px;
  transform: translateY(-50%);
}

.carousel .next {
  position: absolute;
  top: 50%;
  right: 1px;
  transform: translateY(-50%);
}

.carousel button {
  border: 0;
  background-color: Transparent;
  font-size: 20px;
}

.carousel .page {
  text-align: center;
}
.carousel .dot {
  cursor: pointer;
  height: 5px;
  width: 20px;
  margin: 0 2px;
  background-color: #ddd;
  display: inline-flex;
  transition: background-color 0.6s ease;
}

.carousel button:hover,
.dot:hover,
.active {
  background-color: #717171;
  opacity: 1;
}
