.landscape-main-panel {
  max-height: inherit;
  width: 100%;
}

.landscape-main-panel::before {
  content: "";
  z-index: -1;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: url("assets/img/portrait-background.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  filter: opacity(0.3);
}

.landscape-origin-scale-div {
  transform-origin: bottom center;
  transform: scale(0.95);
  transition: 1.2s;
}

.landscape-scaled-div {
  transform-origin: bottom center;
  transform: scale(0.65);
  transition: 1.2s;
}